// while clicking on Beacon-DTX button in the ribbon bar pthen this showTaskPane function will triggered
// showTaskPane fuction which opens the taskpane
// if this function is get triggered while a taskpane already opened , it has no effect
async function showTaskapne(event) {
  Office.addin.showAsTaskpane().then(function () {});
  event.completed();
}

g.showTaskapne = showTaskapne;
